.banner-full{
  width: 100%;
  height: 700px;
  float: left;
  background-image: url("../images/banner-home.jpg");
  background-color: $bg-banner-full;
  background-size: cover;
    h1{
      width: 100%;
      height: auto;
      float: left;
      margin: 238px 0 0;
      text-transform: uppercase;
      text-align: center;
      font-family: $font-lato;
      font-weight: 300;
      font-size: 54px;
      color: $text-banner;
      line-height: 80px;
    }
    p{
      width: 100%;
      height: auto;
      float: left;
      margin: 25px 0 0;
      text-align: center;
      font-family: $font-lato;
      font-weight: 700;
      font-size: 25px;
      color: $text-banner;
    }
}
